.group {
    margin-bottom: 20px;
}

.wraper {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background: url('../../assert/login/login_wraper.jpg') navy;
    background-size: auto 100%;
}

.login-wraper {
    width: 400px;
    padding: 40px 20px;
    border: 1px solid #ccc;
    border-radius: 3px;
    background: #fff;
    position: absolute;
    right: 15%;
    top: 25%;
    box-sizing: border-box;
}